.body{
    background-color: rgb(0, 132, 255);
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.app-body{
    margin-top: 20px;
    border-radius: 10px;
    background-color:white;
    min-width: 500px;
   min-height: 100vh;
  
    
}
.quiz-header{
    padding-top: 10px;
    text-align: center;
}
.test-container{
    margin-top: 30px;
}
.quiz-list{
    margin-top: 10px;
}
.test-header{
    text-align: center;
    padding: 0 15px;
}
.counter{
    padding-left: 15px;
}

.answer{
    max-width: 300px;
    cursor: pointer;
}
.quiz-finish,.quiz-finish-counter,.quiz-return-home{
padding-left: 15px;
}
.quiz-finish{
    font-weight: bold;
    padding-bottom: 10px;
}
.quiz-finish-counter{
    padding-bottom: 10px;
}
.quiz-return-home{
    cursor: pointer;
}
.quiz-list-item{
    cursor: pointer;
}